import { Model } from '@vuex-orm/core';

export default class Dua extends Model {
    static entity = 'duas';

    static fields () {
        return {
            id: this.increment(),
            cod: this.string(''),
            name: this.string(''),
            disabled: this.boolean(false),
            created_at: this.string(new Date().toISOString().substr(0, 10)),
            update_at: this.string(new Date().toISOString().substr(0, 10)),
        };
    }
};
